<template>
  <div class="form">
    <el-form ref="form" :model="formData" label-width="80px">
      <el-form-item label="名称:">
        <el-input v-model="formData.name" style="width: 300px;"></el-input>
      </el-form-item>
      <el-form-item label="价格:">
        <el-input v-model="formData.price" style="width: 300px;">
          <template slot="append">元</template>
        </el-input>
      </el-form-item>
      <el-form-item label="单位:">
        <el-input v-model="formData.unit" style="width: 300px;"></el-input>
      </el-form-item>
      <el-form-item label="分类:">
        <el-select v-model="formData.category_id" placeholder="请选择">
          <el-option v-for="(item, index) of category" :key="index" :label="item.name" :value="item.id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="显示顺序:">
        <el-input-number v-model="formData.display_order" size="small" :min="1" label="显示顺序"></el-input-number>
      </el-form-item>
      <el-form-item label="供应商:">
        <el-checkbox-group v-model="selected_suppliers">
          <el-checkbox v-for="(item, index) of supplier" :label="item.name" :key="index">{{item.name}}</el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item label="进货渠道:">
        <el-checkbox-group v-model="selected_purchase_channels">
          <el-checkbox v-for="(item, index) of channel" :label="item.name" :key="index">{{item.name}}</el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item label="合格:">
        <el-radio v-model="formData.qcpass" :label="true">合格</el-radio>
        <el-radio v-model="formData.qcpass" :label="false">不合格</el-radio>
      </el-form-item>
      <el-form-item label="介绍:">
        <el-input type="textarea" :rows="3" placeholder="请输入内容" v-model="formData.note" style="width: 90%">
        </el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submit">提交</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>

  const formData = {
    name: "",
    price: "",
    qcpass: true,
    suppliers: "",
    unit: "",
    purchase_channels: "",
    display_order: 1,
    note: "",
    category_id: null,
  }
  // import Product from "@/request/Product"
  // const productRequest = new Product();
  import Category from "@/request/Category"
  const categoryRequest = new Category();
  import Supplier from "@/request/Supplier"
  const supplierRequest = new Supplier();
  import Channel from "@/request/Channel"
  const channelRequest = new Channel();
  
  export default {
    props: ["editing_product", "isEdit"],
    async created() {
      let res = await categoryRequest.getAll()
      this.category = res.rows
      let res1 = await supplierRequest.getAll()
      this.supplier = res1.rows
      let res2 = await channelRequest.getAll()
      this.channel = res2.rows
      this.change()
    },
    data() {
      return {
        category: [],
        supplier: [],
        channel: [],
        selected_suppliers: [],
        selected_purchase_channels: [],
        formData: {
          name: "",
          price: "",
          qcpass: true,
          suppliers: "",
          unit: "",
          purchase_channels: "",
          display_order: 1,
          category_id: null,
        }
      }
    },
    watch: {
      editing_product() {
        this.change()
      }
    },
    methods: {
      change() {
        if (!this.editing_product) {
          this.formData = formData
          return false
        }
        this.formData = this.editing_product
        this.selected_suppliers = []
        this.selected_purchase_channels = []
        this.selected_suppliers = this.editing_product.suppliers.split(",")
        this.selected_purchase_channels = this.editing_product.purchase_channels.split(",")
        return false
      },
      submit() {
        this.formData.suppliers = this.selected_suppliers.filter(item => item != "").join(",")
        this.formData.purchase_channels = this.selected_purchase_channels.filter(item => item != "").join(",")
        this.$emit("submit", this.formData)
      }
    }
  }
</script>