<template>
  <div>
    <div>
      <el-form ref="form" :model="formData" label-width="80px" style="margin-top: 30px;">
        <el-form-item label="选择分类:">
          <el-select v-model="formData.category_id" placeholder="请选择" @change="getProduct">
            <el-option v-for="(item, index) of category" :key="index" :label="item.name" :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
    </div>
    <el-drawer :modal="false" :visible.sync="drawer" direction="rtl" size="500px">
      <product_form v-if="editModal" :isEdit="true" :editing_product="currentEditingProduct" @submit="editSubmit"></product_form>
      <product_form v-else :isEdit="false" @submit="createSubmit"></product_form>
    </el-drawer>
    <el-table :data="tableData" stripe style="width: 100%">
      <el-table-column prop="name" label="分类名">
        <template slot-scope="scope">
          {{scope.row.name}}
        </template>
      </el-table-column>
      <el-table-column prop="price" label="价格">
        <template slot-scope="scope">
          <template v-if="scope.row.editPrice">
            <el-input v-model="scope.row.price" style="width: 100px" size="small"
              @keyup.enter.native="confirmPrice(scope.row)" placeholder=""></el-input>
            <el-button @click="confirmPrice(scope.row)" size="small">确定</el-button>
          </template>
          <template v-else>
            {{scope.row.price}}{{scope.row.unit}}
            <el-button @click="editPrice(scope.row)" type="text" size="small">改价</el-button>
          </template>
        </template>
      </el-table-column>
      <el-table-column prop="display_order" label="显示顺序">
        <template slot-scope="scope">
          <template v-if="scope.row.editOrder">
            <el-input-number v-model="scope.row.display_order" size="small" :min="1"></el-input-number>
            <el-button @click="confirmOrder(scope.row)" size="small">确定</el-button>
          </template>
          <template v-else>
            {{scope.row.display_order}}
            <el-button @click="editOrder(scope.row)" type="text" size="small">改变顺序</el-button>
          </template>
        </template>
      </el-table-column>
      <el-table-column prop="suppliers" label="供应商">
        <template slot-scope="scope">
          {{scope.row.suppliers}}
        </template>
      </el-table-column>
      <el-table-column prop="purchase_channels" label="进货渠道">
        <template slot-scope="scope">
          {{scope.row.purchase_channels}}
        </template>
      </el-table-column>
      <el-table-column fixed="right" label="操作" width="150">
        <template slot-scope="scope">
          <el-button @click="edit(scope.row)" type="text" size="small">编辑</el-button>
          <el-button @click="handleDelete(scope.row)" type="text" size="small">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-tooltip class="item" effect="dark" content="新增" placement="top">
      <el-button class="fixedaddbutton" icon="el-icon-plus" type="primary" circle @click="add"></el-button>
    </el-tooltip>
  </div>
</template>

<script>
  import product_form from "./components/product_form";
  import Category from "@/request/Category"
  const categoryRequest = new Category();
  import Product from "@/request/Product"
  const productRequest = new Product();
  export default {
    async created() {
      let res = await categoryRequest.getAll();
      this.category = res.rows
      this.formData.category_id = this.category[0].id
      await this.getProduct()
    },
    components: {
      product_form
    },
    data() {
      return {
        drawer: false,
        editModal: false,
        category: [],
        tableData: [],
        formData: {
          category_id: undefined
        },
        currentEditingProduct: {}
      }
    },
    methods: {
      async getProduct() {
        this.tableData = []
        let options = {
          category_id: this.formData.category_id
        }
        let res = await productRequest.get(options);
        for (const item of res.rows) {
          item.editPrice = false
          item.editOrder = false
          this.tableData.push(item)
        }
      },
      async add() {
        this.drawer = true;
        this.editModal = false;
      },
      editPrice(row) {
        row.editPrice = true
      },
      editOrder(row) {
        row.editOrder = true
      },
      edit(row) {
        
        this.currentEditingProduct = row
        setTimeout(() => {
          this.drawer = true;
          this.editModal = true;
        }, 10);
      },
      async handleDelete(row) {
        let delelteIt = window.confirm("确认删除?");
        if (!delelteIt) return false
        let res = await productRequest.deleteById(row.id)
        if (!res) return false
        let index = this.tableData.findIndex(item => item.id == row.id)
        
        if (index == -1) return false
        this.tableData.splice(index, 1);
      },
      async confirmPrice(row) {
        let data = {
          price: row.price
        }
        let res = await this.confirm(row, data)
        if (res) {
          row.editPrice = false
        }
      },
      async confirmOrder(row) {
        let data = {
          display_order: row.display_order
        }
        let res = await this.confirm(row, data)
        if (res) {
          row.editOrder = false
        }
      },
      async confirm(row, data) {
        
        let res = await productRequest.updateById(row.id, data)
        if (res.statusCode) {
          alert(res.message)
          return false
        }
        return true
      },
      async editSubmit(data) {
        let res = await productRequest.updateById(data.id, data);
        if (res.statusCode) {
          alert(res.message)
          return false
        }
        if (res) {
          alert("修改成功!");
          this.drawer = false;
        }
      },
      async createSubmit(data) {
        let res = await productRequest.create(data);
        if (res.statusCode) {
          alert(res.message)
          return false
        }
        if (res.id) {
          alert("添加成功!");
          this.drawer = false;
          this.tableData.push(res)
        }
      }
    }
  }
</script>

<style scoped lang="scss">
  .main {}
</style>